  margin-right: 6px;\n}\ninput[data-v-133827f7] {\n  text-transform: capitalize;\n}\n.score-blok[data-v-133827f7] {\n  background: #2ecc71;\n  padding: 20px;\n  text-align: center;\n  color: white;\n  border-radius: 6px;\n  margin-bottom: 20px;\n}\n.score-blok .titel[data-v-133827f7] {\n  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;\n  font-size: 19px;\n  font-weight: bold;\n  text-align: center;\n}\n.score-blok .score-titel[data-v-133827f7] {\n  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;\n  font-weight: bold;\n  margin-top: 18px;\n}\n.score-blok .score[data-v-133827f7] {\n  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;\n  font-size: 50px;\n  line-height: 50px;\n  font-weight: 300;\n}\n", "",{"version":3,"sources":["webpack://./../js/components/namen-leren/NamenLeren.vue"],"names":[],"mappings":";AAibA;EACA,YAAA;EACA,WAAA;EACA,kBAAA;EACA,mBAAA;AACA;AAEA;EACA,WAAA;EACA,YAAA;EACA,kCAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,YAAA;EACA,aAAA;EACA,iBAAA;EACA;SACA;EACA,gBAAA;AACA;AAEA;EACA,aAAA;AACA;AAEA;EACA,YAAA;EACA,aAAA;EACA,sBAAA;EACA,4BAAA;EACA,kCAAA;AACA;AAEA;EACA,kBAAA;EACA,mBAAA;EACA,YAAA;EACA,gBAAA;EACA,gBAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,qBAAA;EACA,eAAA;EACA,iBAAA;EACA,sBAAA;EACA,YAAA;EACA,qBAAA;AACA;AAEA;EACA,qBAAA;EACA,YAAA;AACA;AAEA;EACA,qBAAA;EACA,sBAAA;EACA,mBAAA;EACA,6BAAA;EACA,sBAAA;AACA;AAEA;EACA,eAAA;EACA,4DAAA;EACA,iBAAA;AACA;AAEA;EACA,iBAAA;AACA;AAEA;EACA,4DAAA;EACA,eAAA;EACA,iBAAA;AACA;AAEA;EACA,iBAAA;AACA;AAEA;EACA,0BAAA;AACA;AAEA;EACA,mBAAA;EACA,aAAA;EACA,kBAAA;EACA,YAAA;EACA,kBAAA;EACA,mBAAA;AACA;AAEA;EACA,4DAAA;EACA,eAAA;EACA,iBAAA;EACA,kBAAA;AACA;AAEA;EACA,4DAAA;EACA,iBAAA;EACA,gBAAA;AACA;AAEA;EACA,4DAAA;EACA,eAAA;EACA,iBAAA;EACA,gBAAA;AACA","sourcesContent":["<template>\n  <div>\n    <div v-if=\"finished\" class=\"score-blok\">\n      <div class=\"titel\">\n        {{ titel }}\n      </div>\n      <div class=\"score-titel\">Jouw score:</div>\n      <div class=\"score\">{{ Math.round(percentageGoed) }}%</div>\n    </div>\n    <div v-if=\"!started\">\n      <div class=\"row\">\n        <div class=\"col-sm-6\">\n          <strong class=\"mb-1 block\">Lichting(en)</strong>\n          <div>\n            <input\n              id=\"alleLichtingen\"\n              v-model=\"alleLichtingen\"\n              type=\"checkbox\"\n            />\n            <label for=\"alleLichtingen\">Alle lichtingen</label>\n          </div>\n          <div v-for=\"lichting in lichtingen\" :key=\"lichting\">\n            <input\n              v-if=\"!alleLichtingen\"\n              :id=\"'lichting' + lichting\"\n              v-model=\"lichtingSelectie\"\n              type=\"checkbox\"\n              :value=\"lichting\"\n            />\n            <input\n              v-if=\"alleLichtingen\"\n              type=\"checkbox\"\n              :checked=\"alleLichtingen\"\n              disabled\n            />\n            <label :for=\"'lichting' + lichting\">{{ lichting }}</label>\n          </div>\n        </div>\n        <div class=\"col-sm-6\">\n          <strong class=\"mb-1 block\">Verticale(n)</strong>\n          <div>\n            <input\n              id=\"alleVerticalen\"\n              v-model=\"alleVerticalen\"\n              type=\"checkbox\"\n            />\n            <label for=\"alleVerticalen\">Alle verticalen</label>\n          </div>\n          <div v-for=\"verticale in verticalen\" :key=\"verticale\">\n            <input\n              v-if=\"!alleVerticalen\"\n              :id=\"'verticale' + verticale\"\n              v-model=\"verticaleSelectie\"\n              type=\"checkbox\"\n              :value=\"verticale\"\n            />\n            <input\n              v-if=\"alleVerticalen\"\n              type=\"checkbox\"\n              :checked=\"alleVerticalen\"\n              disabled\n            />\n            <label :for=\"'verticale' + verticale\">{{ verticale }}</label>\n          </div>\n        </div>\n      </div>\n      <div>\n        <strong class=\"mb-1 block\">Onderkant verbergen</strong>\n        <div>\n          <input\n            id=\"verbergOnderkant\"\n            v-model=\"verbergOnderkant\"\n            type=\"checkbox\"\n          />\n          <label for=\"verbergOnderkant\">Voorkom leesbare namen op foto's</label>\n        </div>\n      </div>\n      <strong class=\"mt-3 mb-1 block\">Antwoordmethode</strong>\n      <select v-model=\"antwoordMethode\" class=\"form-control\">\n        <option value=\"voornaam\">Voornaam</option>\n        <option value=\"achternaam\">Achternaam</option>\n        <option value=\"combi\">Voor- en achternaam</option>\n        <option value=\"civi\">Achternaam en achtervoegsel</option>\n      </select>\n      <a\n        href=\"#\"\n        class=\"btn btn-primary btn-block mt-3\"\n        :class=\"{ disabled: !klaarVoorDeStart }\"\n        @click.prevent=\"start\"\n        >Start met {{ aantal }} {{ aantal === 1 ? 'lid' : 'leden' }}</a\n      >\n    </div>\n    <div v-else-if=\"!finished\">\n      <div class=\"progress\">\n        <div class=\"correct\" :style=\"{ width: percentageGoed + '%' }\" />\n        <div class=\"again\" :style=\"{ width: percentageOpnieuw + '%' }\" />\n        <div class=\"wrong\" :style=\"{ width: percentageFout + '%' }\" />\n      </div>\n      <div v-if=\"laatste\" class=\"laatste\" :class=\"{ goed: laatsteGoed }\">\n        <img :src=\"'/profiel/pasfoto/' + laatste.uid + '.jpg'\" alt=\"\" />\n        <div class=\"info\">\n          <div class=\"naam\">\n            <span\n              :class=\"{\n                bold:\n                  antwoordMethode === 'voornaam' || antwoordMethode === 'combi',\n              }\"\n              >{{ laatste.voornaam }}</span\n            >\n            <span\n              :class=\"{\n                bold:\n                  antwoordMethode === 'achternaam' ||\n                  antwoordMethode === 'combi' ||\n                  antwoordMethode === 'civi',\n              }\"\n              >{{ laatste.tussenvoegsel }} {{ laatste.achternaam }}</span\n            >\n            <span\n              v-if=\"laatste.postfix\"\n              :class=\"{ bold: antwoordMethode === 'civi' }\"\n              >{{ laatste.postfix }}</span\n            >\n          </div>\n          <div class=\"tekst\">\n            <span>{{ laatste.lichting }}</span>\n            <span v-if=\"laatste.verticale && laatste.verticale !== 'Geen'\">{{\n              laatste.verticale\n            }}</span>\n            <span>{{ laatste.studie }}</span>\n          </div>\n        </div>\n        <Icon icon=\"check\" v-if=\"laatsteGoed\" />\n        <Icon icon=\"xmark\" v-else />\n      </div>\n      <div\n        class=\"pasfotoContainer\"\n        :class=\"{ onderkantVerbergen: verbergOnderkant }\"\n      >\n        <div\n          :style=\"{\n            'background-image': 'url(/profiel/pasfoto/' + huidig.uid + '.jpg)',\n          }\"\n          class=\"pasfoto\"\n        />\n      </div>\n      <strong v-if=\"antwoordMethode === 'voornaam'\" class=\"mb-1 block\"\n        >Voornaam:</strong\n      >\n      <strong v-if=\"antwoordMethode === 'achternaam'\" class=\"mb-1 block\"\n        >Achternaam:</strong\n      >\n      <strong v-if=\"antwoordMethode === 'combi'\" class=\"mb-1 block\"\n        >Voor- en achternaam:</strong\n      >\n      <strong v-if=\"antwoordMethode === 'civi'\" class=\"mb-1 block\"\n        >Achternaam en achtervoegsel:</strong\n      >\n      <input\n        type=\"text\"\n        class=\"form-control\"\n        :value=\"ingevuld\"\n        autofocus\n        @input=\"ingevuld = $event.target.value\"\n        @keydown.enter=\"controleer\"\n      />\n    </div>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport Vue from 'vue';\nimport { Component, Prop } from 'vue-property-decorator';\nimport Icon from '../common/Icon.vue';\n\nconst shuffle = <T>(array: T[]) => {\n  let currentIndex = array.length;\n  let temporaryValue;\n  let randomIndex;\n\n  // While there remain elements to shuffle...\n  while (0 !== currentIndex) {\n    // Pick a remaining element...\n    randomIndex = Math.floor(Math.random() * currentIndex);\n    currentIndex -= 1;\n\n    // And swap it with the current element.\n    temporaryValue = array[currentIndex];\n    array[currentIndex] = array[randomIndex];\n    array[randomIndex] = temporaryValue;\n  }\n\n  return array;\n};\n\nconst preloaded: string[] = [];\n\nconst preloadImage = (url: string) => {\n  if (preloaded.includes(url)) {\n    return;\n  }\n  preloaded.push(url);\n  const img = new Image();\n  img.src = url;\n};\n\nconst uniq: <T>(arr: T[]) => T[] = (arr) => {\n  return Array.from(new Set(arr));\n};\n\ninterface Lid {\n  uid: string;\n  lichting: string;\n  verticale: string;\n  tussenvoegsel: string;\n  achternaam: string;\n  voornaam: string;\n  postfix: string;\n}\n\n@Component({\n  components: { Icon },\n})\nexport default class NamenLeren extends Vue {\n  @Prop()\n  leden: Lid[];\n\n  // Config\n  alleLichtingen = false;\n  alleVerticalen = true;\n  lichtingSelectie: string[] = [];\n  verticaleSelectie: string[] = [];\n  antwoordMethode = 'voornaam';\n  aantalPerKeer = 5;\n\n  // Game state\n  verbergOnderkant = true;\n  started = false;\n  finished = false;\n  goed: Lid[] = [];\n  opnieuw: Lid[] = [];\n  fout: Lid[] = [];\n  todo: Lid[] = [];\n  laatste: Lid | null = null;\n  laatsteGoed: boolean | null = null;\n  huidig: Lid | null = null;\n  ingevuld = '';\n  titel = '';\n\n  private get aantal() {\n    return this.gefilterdeLeden.length;\n  }\n\n  private get gefilterdeLeden() {\n    return this.leden.filter(\n      (lid: Lid) =>\n        (this.alleLichtingen || this.lichtingSelectie.includes(lid.lichting)) &&\n        (this.alleVerticalen || this.verticaleSelectie.includes(lid.verticale))\n    );\n  }\n\n  private get lichtingen() {\n    return uniq(this.leden.map((lid) => lid.lichting)).sort();\n  }\n\n  private get verticalen() {\n    return uniq(this.leden.map((lid) => lid.verticale)).sort();\n  }\n\n  private get klaarVoorDeStart() {\n    return this.gefilterdeLeden.length > 0;\n  }\n\n  private get totaalAantal() {\n    return (\n      this.todo.length +\n      this.goed.length +\n      this.opnieuw.length +\n      this.fout.length\n    );\n  }\n\n  private get percentageGoed() {\n    return this.totaalAantal > 0\n      ? (this.goed.length / this.totaalAantal) * 100\n      : 0;\n  }\n\n  private get percentageOpnieuw() {\n    return this.totaalAantal > 0\n      ? (this.opnieuw.length / this.totaalAantal) * 100\n      : 0;\n  }\n\n  private get percentageFout() {\n    return this.totaalAantal > 0\n      ? (this.fout.length / this.totaalAantal) * 100\n      : 0;\n  }\n\n  private start() {\n    if (!this.klaarVoorDeStart) {\n      return;\n    }\n    this.started = true;\n    this.goed = [];\n    this.opnieuw = [];\n    this.fout = [];\n    this.todo = this.gefilterdeLeden;\n    shuffle(this.todo);\n    this.huidig = null;\n    this.laatste = null;\n    this.finished = false;\n    this.volgende();\n    this.titel = this.bouwTitel();\n    document.title = `C.S.R. Delft - Namen ${this.titel} leren`;\n    window.scrollTo(0, 0);\n  }\n\n  private volgende() {\n    const choice = this.fout.concat(\n      this.todo.slice(0, Math.max(this.aantalPerKeer - this.fout.length, 0))\n    );\n    const pickable = choice.filter(\n      (lid) =>\n        choice.length === 1 || !this.huidig || lid.uid !== this.huidig.uid\n    );\n    if (pickable.length > 0) {\n      for (const lid of pickable) {\n        preloadImage('/profiel/pasfoto/' + lid.uid + '.jpg');\n      }\n      this.huidig = pickable[Math.floor(Math.random() * pickable.length)];\n      this.ingevuld = '';\n    } else {\n      this.finished = true;\n      this.started = false;\n    }\n  }\n\n  private controleer() {\n    if (this.huidig == null) {\n      throw new Error('huidig niet gezet');\n    }\n    // Antwoord vormen\n    const onderdelen = [];\n    if (\n      this.antwoordMethode === 'voornaam' ||\n      this.antwoordMethode === 'combi'\n    ) {\n      onderdelen.push(this.huidig.voornaam);\n    }\n    if (\n      this.antwoordMethode === 'achternaam' ||\n      this.antwoordMethode === 'combi' ||\n      this.antwoordMethode === 'civi'\n    ) {\n      if (this.huidig.tussenvoegsel) {\n        onderdelen.push(this.huidig.tussenvoegsel);\n      }\n      onderdelen.push(this.huidig.achternaam);\n    }\n    if (this.antwoordMethode === 'civi') {\n      if (this.huidig.postfix) {\n        onderdelen.push(this.huidig.postfix);\n      }\n    }\n    const antwoord = onderdelen.map((s) => s.trim()).join(' ');\n\n    // Antwoord checken\n    this.laatste = this.huidig;\n    this.laatsteGoed =\n      antwoord.toLowerCase().replace('.', '') ===\n      this.ingevuld.toLowerCase().replace('.', '');\n\n    // Verwijderen uit oude lijst en toevoegen aan nieuwe lijst\n    let index = this.todo.findIndex((lid) => lid.uid === this.huidig?.uid);\n    if (index === -1) {\n      // Fout lijst\n      if (this.laatsteGoed) {\n        index = this.fout.findIndex((lid) => lid.uid === this.huidig?.uid);\n        this.fout.splice(index, 1);\n        this.opnieuw.push(this.huidig);\n      }\n    } else {\n      // Te doen lijst\n      this.todo.splice(index, 1);\n      if (this.laatsteGoed) {\n        this.goed.push(this.huidig);\n      } else {\n        this.fout.push(this.huidig);\n      }\n    }\n\n    this.volgende();\n  }\n\n  private bouwTitel() {\n    if (this.alleLichtingen && this.alleVerticalen) {\n      return 'Alle leden';\n    }\n\n    let titel = '';\n    if (!this.alleLichtingen) {\n      this.lichtingSelectie.sort();\n      titel += 'Lichting ';\n      titel += this.lichtingSelectie\n        .slice(0, this.lichtingSelectie.length - 1)\n        .join(', ');\n      if (this.lichtingSelectie.length > 1) {\n        titel += ' & ';\n      }\n      titel += this.lichtingSelectie[this.lichtingSelectie.length - 1];\n    }\n    if (!this.alleVerticalen) {\n      if (titel) {\n        titel += ', ';\n      }\n      this.verticaleSelectie.sort();\n      titel += this.verticaleSelectie\n        .slice(0, this.verticaleSelectie.length - 1)\n        .join(', ');\n      if (this.verticaleSelectie.length > 1) {\n        titel += ' & ';\n      }\n      titel += this.verticaleSelectie[this.verticaleSelectie.length - 1];\n    }\n\n    return titel;\n  }\n}\n</script>\n\n<style scoped>\n.progress {\n  height: 20px;\n  width: 100%;\n  border-radius: 3px;\n  background: #adadad;\n}\n\n.progress div {\n  float: left;\n  height: 100%;\n  transition: width ease-in-out 0.5s;\n}\n\n.progress div.correct {\n  background: #2ecc71;\n}\n\n.progress div.again {\n  background: #f1c40f;\n}\n\n.progress div.wrong {\n  background: #c0392b;\n}\n\n.pasfotoContainer {\n  width: 170px;\n  height: 255px;\n  margin: 15px auto;\n  background: url('../../../images/loading-fb.gif') no-repeat center center\n    white;\n  overflow: hidden;\n}\n\n.pasfotoContainer.onderkantVerbergen {\n  height: 150px;\n}\n\n.pasfotoContainer .pasfoto {\n  width: 170px;\n  height: 255px;\n  background-size: cover;\n  background-repeat: no-repeat;\n  background-position: center center;\n}\n\n.laatste {\n  border-radius: 6px;\n  background: #c0392b;\n  color: white;\n  overflow: hidden;\n  margin-top: 10px;\n}\n\n.laatste.goed {\n  background: #2ecc71;\n}\n\n.laatste .fas {\n  display: inline-block;\n  font-size: 26px;\n  line-height: 90px;\n  vertical-align: middle;\n  float: right;\n  margin: 0 18px 0 15px;\n}\n\n.laatste img {\n  display: inline-block;\n  height: 90px;\n}\n\n.laatste .info {\n  display: inline-block;\n  padding: 29px 0 0 15px;\n  vertical-align: top;\n  max-width: calc(100% - 123px);\n  box-sizing: border-box;\n}\n\n.laatste .info .naam {\n  font-size: 19px;\n  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;\n  line-height: 19px;\n}\n\n.laatste .info .naam .bold {\n  font-weight: bold;\n}\n\n.laatste .info .tekst {\n  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;\n  font-size: 15px;\n  line-height: 24px;\n}\n\n.laatste .info .tekst span {\n  margin-right: 6px;\n}\n\ninput {\n  text-transform: capitalize;\n}\n\n.score-blok {\n  background: #2ecc71;\n  padding: 20px;\n  text-align: center;\n  color: white;\n  border-radius: 6px;\n  margin-bottom: 20px;\n}\n\n.score-blok .titel {\n  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;\n  font-size: 19px;\n  font-weight: bold;\n  text-align: center;\n}\n\n.score-blok .score-titel {\n  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;\n  font-weight: bold;\n  margin-top: 18px;\n}\n\n.score-blok .score {\n  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;\n  font-size: 50px;\n  line-height: 50px;\n  font-weight: 300;\n}\n</style>\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
